
import { Options, setup, Vue } from 'vue-class-component';
import { Watch } from 'vue-property-decorator';

import {
  useSmsCodeConfirmation,
  useSmsCodeAuthorization,
  useSmsCodeCooldown,
  useCheckUserProfile,
  useUpdateUserData,
  useUserInfo,
  useRedirect,
  useDataLayerInfo,
} from '@/composable';

import SpinnerIcon from '@/ui/atoms/RIcon/animated/Spinner.vue';
import {
  RInput,
  RBtn,
} from '@/ui/atoms';

@Options({
  components: {
    RInput,
    RBtn,
    SpinnerIcon,
  },
})
export default class Authorization extends Vue {
  smsCodeAuthorization = setup(useSmsCodeAuthorization);

  smsCodeConfirmation = setup(useSmsCodeConfirmation);

  smsCodeCooldown = setup(useSmsCodeCooldown);

  checkUserProfile = setup(useCheckUserProfile);

  user = setup(useUpdateUserData);

  userInfo = setup(useUserInfo);

  redirect = setup(useRedirect);

  dataLayerInfo = setup(useDataLayerInfo);

  confirmationCode = '';

  // eslint-disable-next-line class-methods-use-this
  get isCodeFilled(): boolean {
    const requiredSmsCodeLength = 4;

    return this.confirmationCode.length === requiredSmsCodeLength;
  }

  get isCodeFilledWithError(): boolean {
    return this.isCodeFilled
      && !!this.smsCodeConfirmation.authorizationConfirmation.error;
  }

  sendNewCode(): void {
    this.$analyticsUtils.pushEvent('sms_code_resend', {
      eventLabel: this.dataLayerInfo.flowLabel ?? 'telemed',
      eventCategory: 'registration',
    });

    this.smsCodeAuthorization.authorize();
  }

  mounted(): void {
    if (!this.userInfo.userInfo.data?.PhoneNumber) {
      this.$router.push({
        name: this.$routeNames.PHONE_NUMBER_CHANGE,
        query: {
          redirectTo: this.redirect.redirectToPath,
        },
      });
    }
  }

  @Watch('confirmationCode')
  async onConfirmationCodeChange(confirmationCode: string): Promise<void> {
    if (!this.isCodeFilled) {
      return;
    }

    await this.smsCodeConfirmation.authorizeByConfirmationCode(confirmationCode);

    if (this.smsCodeConfirmation.authorizationConfirmation.error) {
      this.$analyticsUtils.pushEvent('sms_code_error', {
        eventLabel: this.dataLayerInfo.flowLabel ?? 'telemed',
        eventCategory: 'registration',
        error: this.smsCodeConfirmation.authorizationConfirmation.error,
      });
      return;
    }

    this.$analyticsUtils.pushEvent('sms_code_input_done', {
      eventLabel: this.dataLayerInfo.flowLabel ?? 'telemed',
      eventCategory: 'registration',
      // eslint-disable-next-line @typescript-eslint/camelcase
      user_id: this.smsCodeConfirmation.authorizationConfirmation.data?.userId ?? '',
    });

    if (this.redirect.redirectToPath.includes(this.$routePaths.USER_DATA)) {
      this.checkUserProfile.skipProfileUpdateIfUpdated();
    } else {
      this.redirect.redirect();
    }
  }
}
